import React from 'react';
import logo from './assets/img/tree/MichaelsTree.svg';
import './assets/scss/outerlooper.scss';

class App extends React.Component {

  state = {
    headerClass: 'header-top'
  }

  componentDidMount(){
    window.addEventListener('scroll', () => {
    
      let headerClass = 'header-scroll';

      if (window.scrollY > 10){
        headerClass = 'header-scroll';
      } else {
        headerClass = 'header-top';
      }

      this.setState({ headerClass });
    });
  }

  render() {
    return (
      <div className="App">

        <header id="header" className={this.state.headerClass}>
          <h1>Outerlooper</h1>
          <p><span role="img" aria-label="">&#10024;</span>&nbsp; Michael Cooper</p>
        </header>

        <div id="tree-svg-wrap">
          <img src={logo} className="App-logo" alt="logo" />
        </div>

        <div>
          <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
        </div>

        <div id="orb"></div>
        <span id="moon"></span>
        <div class='stars'></div>
        <div class='twinkling'></div>
        <div class='clouds'></div>

        <footer id="footer">
        <span role="img" aria-label="">&#10024;</span>&copy; Outerlooper
          {/* <span>|</span>
          Contacts */}
        </footer>

      </div>
    );
  }
  
}

export default App;
